import {Link} from "react-router-dom";

function Post({posts, loading}) {
    return (
        <>
            {loading ? <h3>Loading...</h3> :
                <div className={'container'} >
                    <div className={'row justify-content-center'}>
                        {posts.map(p =>
                            <Link  data-toggle="tooltip" title="Click To See Info" className={'card mx-1 my-3 shadow rounded  col-lg-5 col-8'} to={`/info/${p.id}`}>
                                <div className={'card-body'}>
                                    <img src={p.url} className="card-img " alt={p.id}/>
                                    <h4 className={'card-title text-center text-info mt-2'}>{p.title} </h4>
                                </div>
                            </Link>
                        )}
                    </div>
                </div>
            }
        </>
    )
}

export default Post;