import Post from "../Post";
import Pagination from "../Pagination";
import {useEffect, useState} from "react";
import axios from "axios";


function Home() {

    const [loading, setLoading] = useState(false)
    const [posts, setPosts] = useState([])
    const [pages, setPages] = useState({
        currentPage: 1,
        postperPage: 10
    });


    useEffect(() => {
        let getdata = () => {
            setLoading(true)
            axios.get('https://jsonplaceholder.typicode.com/photos')
                .then(res => setPosts(res.data))
                .catch(err => console.log(err))
            setLoading(false)
        }
        getdata()
    }, [])


    const lastindex = pages.currentPage * pages.postperPage;
    const firstindexOfpost = lastindex - pages.postperPage;
    const currentPosts = posts.slice(firstindexOfpost, lastindex);

    return (
        <>
            <Pagination totalpost={posts.length} postperPage={pages.postperPage} current={pages.currentPage}
                        setcurrent={(number) => setPages(prevState => ({...prevState, currentPage: number}))}/>
            <Post posts={currentPosts} loading={loading}/>
            <Pagination totalpost={posts.length} postperPage={pages.postperPage} current={pages.currentPage}
                        setcurrent={(number) => setPages(prevState => ({...prevState, currentPage: number}))}/>
        </>
    )
}

export default Home;