import {Dropdown} from "react-bootstrap";
import menu from '../img/menu.svg'
import {NavLink} from "react-router-dom";

function Header() {
    return (
        <div className={'container-fluid bg-primary'}>
            <div className={'row mb-2 p-4 d-flex justify-content-between'}>
                <h3 className={' text-white '}>
                    <NavLink className='text-light' exact to='/'>Home</NavLink>
                </h3>
                <div>
                    <Dropdown className={'d-sm-block d-md-none'}>
                        <Dropdown.Toggle variant="primary" id="dropdown-basic">
                            <img src={menu} alt="menu"/>
                        </Dropdown.Toggle>

                        <Dropdown.Menu>
                            <Dropdown.Item href="#/action-1">About Us</Dropdown.Item>
                            <Dropdown.Item href="#/action-2">Contact Us</Dropdown.Item>
                            <Dropdown.Item href="#/action-3">Something else</Dropdown.Item>
                        </Dropdown.Menu>
                    </Dropdown>

                    <div className={'d-none d-md-block text-light'}>
                        <span className={'m-3'}>About Us</span>
                        <span className={'m-3'}>Contact Us</span>
                        <span className={'m-3'}>Something else</span>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Header;