import axios from "axios";
import {useParams} from 'react-router-dom';
import {useEffect, useState} from "react";

function PostInfo(props) {
    const params = useParams();

    const [post, setPost] = useState({})
    const [loading, setLoadnig] = useState(false)

    useEffect(() => {
        let getdata = () => {
            setLoadnig(true)
            axios.get(`https://jsonplaceholder.typicode.com/photos/${params.id}`)
                .then(res => setPost(res.data))
                .catch(err => console.log(err))
            setLoadnig(false)
        }
        getdata()
    }, [params])

    return (
        <>
            {loading ? <h3>Loading...</h3> :
                <div className={'container'}>
                    <div className={'row justify-content-center'}>
                        <div className={'col-lg-6 col-10'}>
                            <h4 className={'text-center'}>Photo</h4>
                            <hr className={'bg-info'}/>
                            <img className={'img-thumbnail mx-auto d-block'} width={'500px'} height={'500px'} src={post.url} alt="img"/>
                        </div>
                        <div className={'col-lg-6 col-10'}>
                            <h4 className={'text-center'}>Title</h4>
                            <hr className={'bg-info'}/>
                            <p className={'text-center'}>{post.title}</p>
                        </div>
                    </div>
                </div>
            }
        </>
    )
}

export default PostInfo;