function Pagination({postperPage, setcurrent, totalpost, current}) {
    const pageNum = []

    for (let i = 1; i <= (totalpost / postperPage); i++) {
        pageNum.push(i)
    }


    return (
        <div className={'my-3 pagination justify-content-center'}>
            {
                pageNum.slice(current <5 ? 0 : current-4, current + 4).map(num => <span
                    className={' justify-content-around page-item'}>
                    <button onClick={() => setcurrent(num)}
                            className={`btn  ${current === num ? 'btn-bg text-primary' : 'btn-sm text-dark'}`}>
                        {num}
                    </button>
                </span>)
            }
        </div>
    )
}

export default Pagination;